<template>
  <div>
    <el-container>
      <el-header>
        <span>npm</span>
        <a href="https://github.com/luffyli/npm-download-count" target="_blank" class="header-github-link">
          <svg height="60" width="28" viewBox="0 0 16 16" version="1.1" aria-hidden="true" class="octicon octicon-mark-github">
            <path fill-rule="evenodd" d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"></path>
          </svg>
        </a>
      </el-header>
      <el-main>
        <el-form :inline="true" :model="queryForm" :rules="rules" ref="queryForm" class="query-form-inline">
          <el-form-item prop="name">
            <el-input placeholder="" v-model="queryForm.name" class="input-with-select">
              <el-select v-model="queryForm.searchType" slot="prepend" placeholder="">
                <el-option label="" value="packageName"></el-option>
                <el-option label="" value="userName"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="datetime">
            <el-date-picker v-model="queryForm.datetime" type="daterange" align="right" value-format="yyyy-MM-dd" unlink-panels range-separator="" start-placeholder=""
                end-placeholder="" :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
          </el-form-item>
        </el-form>
        <el-card class="box-card" v-show="packageInfo.length > 0">
          <div slot="header" class="clearfix">
            <div class="total-download-tag">
              <span></span>
              <el-tag v-for="(item, index) in packageInfo" :key="index"><a class="link_to_npm" :href="`https://www.npmjs.com/package/${item.name}`" target="_blank">{{ item.name }}</a>{{ item.total }}</el-tag>
            </div>
          </div>
          <div id="chart-render"></div>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import * as echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'
// import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/dataZoom'
import * as http from '@/api'

export default {
  data () {
    let checkDateLength = (rule, value, callback) => {
      let timeLen = new Date(value[1]).getTime() - new Date(value[0]).getTime()
      let dataLen = timeLen / 86400000
      if (dataLen > 546) { // 546
        this.$notify({
          title: '',
          message: '546',
          type: 'warning'
        })
        callback()
      } else {
        callback()
      }
    }

    return {
      fullscreenLoading: true,
      packageInfo: [],
      packageName: '',
      queryForm: {
        searchType: '',
        name: '',
        datetime: []
      },
      npmDataChart: '',
      chartOpention: '',
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        datetime: [
          { required: true, message: '', trigger: 'change' },
          { validator: checkDateLength, trigger: 'change' }
        ]
      }
    }
  },
  mounted () {
    this.queryForm = {
      searchType: this.$route.query.searchType || 'packageName',
      name: this.$route.query.name || 'vue',
      datetime: this.$route.query.datetime.split(',') || [this.dateFormat(new Date().setMonth(new Date().getMonth() - 1)), this.dateFormat()]
    }
    this.npmDataChart = echarts.init(document.getElementById('chart-render'), null, {renderer: 'svg'})
    this.initChartOption()
    if (this.queryForm.searchType === 'userName') {
      this.getUserData()
    } else {
      this.packageName = this.queryForm.name
      this.getPackageData()
    }
    window.onresize = this.npmDataChart.resize
  },
  methods: {
    getPackageData () {
      http.getPackageData(this.queryForm.datetime, this.packageName)
        .then((response) => {
          if (response.status === 404) {
            this.fullscreenLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
            throw new Error('404')
          } else if (response.status === 400) {
            this.$notify({
              title: '',
              message: '365',
              type: 'error'
            })
            throw new Error('400')
          }
          return response.json()
        })
        .then((res) => {
          this.initChartOption()
          this.packageInfo = []
          let packageArr = this.packageName.split(',')
          if (packageArr.length > 1) {
            for (let value of Object.entries(res)) {
              if (value[0]) {
                this.dataHandle(value[0], value[1].downloads, true)
              }
            }
          } else {
            this.dataHandle(this.packageName, res.downloads, false)
          }
          this.fullscreenLoading = false
          this.npmDataChart.setOption(this.chartOpention, true)
          setTimeout(() => {
            this.npmDataChart.resize()
          }, 0)
        })
        .catch((e) => {
          if (e.message !== '404' && e.message !== '400') {
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          }
          this.fullscreenLoading = false
          return Promise.reject(e)
        })
    },
    getUserData () {
      http.getUserData(this.queryForm.name)
        .then((response) => {
          return response.json()
        })
        .then((res) => {
          if (res.total === 0) {
            this.fullscreenLoading = false
            this.$notify({
              title: '',
              message: '',
              type: 'warning'
            })
            return
          }
          let packageArr = [].concat(res.results.map(x => x.package))
          let packageName = []
          packageArr.forEach((item) => {
            packageName.push(item.name)
          })
          this.packageName = packageName.join()
          this.getPackageData()
        })
        .catch((e) => {
          this.fullscreenLoading = false
          console.log(e)
        })
    },
    dataHandle (seriesName, dataArr, isBatch) {
      let downloadsArr = []
      let dayArr = []
      let totalDownload = 0
      for (let dwValue of Array.values(dataArr)) {
        dayArr.push(dwValue.day)
        downloadsArr.push(dwValue.downloads)
        totalDownload += dwValue.downloads
      }
      this.chartOpention.series.push({
        name: seriesName,
        type: 'line',
        data: downloadsArr,
        smooth: true
      })
      this.chartOpention.xAxis.data = dayArr
      if (isBatch) {
        this.chartOpention.legend.data.push(seriesName)
        totalDownload = totalDownload.toLocaleString()
        this.packageInfo.push({
          name: seriesName,
          total: totalDownload
        })
      } else {
        totalDownload = totalDownload.toLocaleString()
        this.packageInfo.push({
          name: seriesName,
          total: totalDownload
        })
      }
    },
    onSubmit () {
      this.$refs['queryForm'].validate((valid) => {
        if (valid) {
          this.fullscreenLoading = true
          let oQuery = { ...this.queryForm }
          oQuery.datetime = this.queryForm.datetime.join()
          this.$router.push({ name: 'home', query: { ...oQuery } })
          if (this.queryForm.searchType === 'packageName') {
            this.packageName = this.queryForm.name
            this.getPackageData()
          } else {
            this.getUserData()
          }
        } else {
          return false
        }
      })
    },
    initChartOption () {
      this.chartOpention = {
        title: {
          left: '3%',
          text: '',
          textStyle: {
            fontSize: 14,
            fontWeight: 500
          }
        },
        legend: {
          data: []
        },
        tooltip: {
          trigger: 'axis'
        },
        dataZoom: [{
          type: 'inside'
        }, {
          handleSize: '80%',
          handleStyle: {
            color: '#fff',
            shadowBlur: 3,
            shadowColor: 'rgba(0, 0, 0, 0.6)',
            shadowOffsetX: 2,
            shadowOffsetY: 2
          }
        }],
        grid: {
          left: '3%',
          right: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#609ee9'
            }
          },
          data: []
        },
        yAxis: {
          type: 'value',
          splitLine: {
            lineStyle: {
              color: ['#D4DFF5']
            }
          },
          axisLine: {
            lineStyle: {
              color: '#609ee9'
            }
          },
          axisLabel: {
            formatter: function (val) {
              return val >= 1000 ? (val / 1000) + 'k' : val
            }
          }
        },
        series: []
      }
    },
    dateFormat (date = (+new Date())) {
      let d = new Date(date)
      let month = (d.getMonth() + 1)
      let day = d.getDate()
      if (month < 10) month = `0${month}`
      if (day < 10) day = `0${day}`
      return `${d.getFullYear()}-${month}-${day}`
    }
  }
}
</script>

<style>
  .el-header {
    background-color: #eee;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .header-github-link {
    float: right;
    line-height: 0;
  }
  #chart-render {
    width: 100%;
    height: 400px;
    margin: 0 auto;
  }
  .query-form-inline {
    text-align: center;
  }
  .el-tag {
    margin-left: 10px;
    margin-bottom: 5px;
  }
  .el-card__header {
    padding: 10px 20px;
  }
  .total-download-tag span {
    font-size: 14px;
  }
  .link_to_npm {
    color: inherit;
    text-decoration: none;
  }
  .link_to_npm:hover {
    text-decoration: underline;
  }
  .el-select .el-input {
    width: 90px;
  }
</style>



